
body {
  background: #222222;
  margin: 10px;
  font-family: 'Lato', sans-serif;
}

.recharts-wrapper .recharts-cartesian-grid-horizontal line:first-child,
.recharts-wrapper .recharts-cartesian-grid-horizontal line:last-child {
      stroke-opacity: 0;
}
.recharts-wrapper .recharts-cartesian-grid-vertical line:first-child,
.recharts-wrapper .recharts-cartesian-grid-vertical line:last-child {
      stroke-opacity: 0;
}

.react-grid-item > .react-resizable-handle::after {
  content: "";
  position: absolute;
  right: 3px;
  bottom: 3px;
  width: 5px;
  height: 5px;
  border-right: 2px solid rgba(102, 77, 77, 0.5);
  border-bottom: 2px solid rgba(102, 77, 77, 0.5);
}